<script>
import "../css/admin.css";
import "../css/amazeui.css";
import "../css/demo.css";
import "../css/optstyle.css";
import "../css/style.css";
import imgZoom from "vue2.0-zoom";
import { get, post } from "axios";

export default {
  components: {
    imgZoom
  },
  data() {
    return {
      counter: 1,
      products: [],
      url: require("../images/01_mid.jpg"),
      url1: require("../images/01.jpg"),
      configs: {
        width: 650,
        height: 450,
        maskWidth: 100,
        maskHeight: 100,
        maskColor: "red",
        maskOpacity: 0.2
      }
    };
  },
  computed: {
    init() {
      if (sessionStorage.getItem("token")) {
        //true,token,
        return `,${sessionStorage.getItem("userName")}`;
      } else {
        return ",";
      }
    }
  },
  methods: {
    reduce() {
      //	console.log(this.$route.params.id)
      console.log(this.products);
      if (this.products[0].quantity < 2) {
        this.products[0].quantity = 1;
      } else {
        this.products[0].quantity -= 1;
      }
    },
    plus() {
      if (this.products[0].quantity > 99) {
        this.products[0].quantity = 100;
      } else {
        this.products[0].quantity += 1;
      }
    },
    addToCart() {
      post(
        "https://api.cat-shop.penkuoer.com/api/v1/shop_carts",
        {
          quantity: this.products[0].quantity,
          product: this.$route.params.id
        },
        {
          headers: {
            Authorization: `bearer ${sessionStorage.getItem("token")}`
          }
        }
      )
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    Login() {
      this.$router.push({
        name: "Login"
      });
    },
    Reg() {
      this.$router.push({
        name: "Reg"
      });
    },
    Home() {
      this.$router.push({
        name: "Home"
      });
    },
    Cart() {
      this.$router.push({
        name: "Cart"
      });
    },
    buy() {
      this.$router.push({
        name: "Cart"
      });
    }
  },
  created() {
    const id = this.$route.params.id;
    console.log(id);
    get(`https://api.cat-shop.penkuoer.com/api/v1/products/${id}`)
      .then(res => {
        console.log(res);
        // console.log(res.data.coverImg)
        this.products.push(res.data);
        console.log(this.products);
      })
      .catch(err => {
        console.log(err);
      });
  }
};
</script>
